import React from 'react';
import autobind from 'class-autobind';
import { readRemoteFile } from 'react-papaparse';
import DataStore from '../datastore'
import Marks from './marks'
import './dashboard.css'
import axios from 'axios'

const KEY_TIMESTAMP = 0;
const KEY_BROTHERNAME = 1;
const KEY_MARK_TYPE = 2;
const KEY_MARK_COUNT = 3;
const KEY_MARK_DESC = 4;
const KEY_PLEDGES = 5;
const KEY_APPROVED = 7;


export default class DashBoard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {pc: null};
        autobind(this);
        this.getData();
    }

    setData(pledges) {
        this.setState({pc: pledges})
    }

    async getData() {

        readRemoteFile("https://docs.google.com/spreadsheets/d/e/2PACX-1vQ1o_778gUABcOu4gAq7zup6TGXf1jRes7CT8kUqJ13LEsj8BiWqROJN8rVm5eKi0v4WsBgIYtvbIQq/pub?gid=326080688&single=true&output=csv", {
            complete: (results) => {                
                console.log(results);

                let pledges = {}

                let marks = results.data;
                marks.sort((a, b) => (a[KEY_TIMESTAMP] > b[KEY_TIMESTAMP]) ? 1 : -1)

                for (let i = 1; i < marks.length; i++) {
                    console.log(marks[i][KEY_APPROVED])
                    if (marks[i][KEY_APPROVED] === "yes") {
                        let mark = new Mark(marks[i][KEY_TIMESTAMP].split(" ")[0], marks[i][KEY_BROTHERNAME], marks[i][KEY_MARK_TYPE].toLowerCase(), parseInt(marks[i][KEY_MARK_COUNT]), marks[i][KEY_MARK_DESC])

                        let pledge_names = marks[i][KEY_PLEDGES].split(", ")
                        pledge_names.forEach(pledge => {
                            pledge = pledge.toLowerCase()
                            if (!(pledge in pledges)) {
                                pledges[pledge] = new Pledge(pledge)
                            }
                            pledges[pledge].marks.push(mark)
                        })
                    }
                }

                let pledge_list = Object.values(pledges)
                pledge_list.sort((a, b) => (a.name > b.name) ? 1 : -1)

                this.setData(pledge_list)
                return
            },
          });


    }

    openPledge(pledge) {
        this.setState({open: pledge.name});
    }

    render() {
        let pc = this.state.pc;
        let open = this.state.open;

        if (!pc)
            return (
            <div className="dash">
                <div className="loader"/>
            </div>);

        let MarkDetails = (marks) => {
            let details = []
            marks.forEach(m => {
                details.push(
                <tr key={m.desc} className="details">
                    <td><Marks type={m.type} count={m.count}/></td>
                    <td className="details">{m.date}</td>
                    <td className="details">{m.brother + ' - ' + m.desc}</td>
                </tr>)
            });
            return details;
        }

        

        let Pledge = (pledge) => {
            return(
                <tr key={pledge.name} onClick={() => this.openPledge(pledge)}>
                    <td>{pledge.name}</td>
                    <td><Marks type="black" count={pledge.getTotal("black")}/></td>
                    <td><Marks type="white" count={pledge.getTotal("white")}/></td>
                </tr>)
        }

        let pledges = [];
        pc.forEach(p => {
            pledges.push(Pledge(p));
            if (open === p.name) {
                pledges = pledges.concat(MarkDetails(p.marks));
            }
        })
            
        return(
            <div className="dash">
                <table>
                    <tbody>
                        <tr>
                            <th width="25%">
                                pledge
                            </th>
                            <th width="20%">
                                black
                            </th>
                            <th >
                                white
                            </th>
                        </tr>
                        {pledges}
                    </tbody>
                </table>
                <br></br>
            </div>
        )
    }
}

class Mark {
    constructor(date, brother, type, count, desc) {
    this.date = date
    this.brother = brother
    this.type = type
    this.count = count
    this.desc = desc
    }
}

class Pledge {
    constructor(name) {
        this.name = name;
        this.marks = [];
    }

    getTotal(type) {
        let total = 0;
        for (let i in this.marks) {
            if (this.marks[i].type === type)
                total += this.marks[i].count;
        }

        return total;
    }
}